/* -------------------------------------------------------------------------- 
	#. Floating Cart
  -------------------------------------------------------------------------- */

// Chrome, Safari, Edge & Opera
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {

  -webkit-appearance: none;
  margin: 0;
}

// Firefox
input[type=number] {
    
  -moz-appearance: textfield;
}

#adfy__floating-cart {

    display: block;
    position: fixed;
    color: #444444;
    background: white;
    background-color: white;

    .adfy_woofc-inner {

        @include flex();
        flex-direction: column;
        height: 100%;
        width: 100%;
        position: relative;
        padding: 30px;

        .adfy__woofc-header {

            @include grid();
            grid-template-columns: auto 30px;
            column-gap: 20px;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 30px;
            padding-bottom: 15px;
            border-bottom: 1px solid var(--adfy_woofc_border_color);

            .adfy__woofc-title {
                
                @include flex();
                gap: 8px;
                align-items: center;
                font-size: 20px;
                margin: 0;
                padding: 0;
                color: #444444;

                .adfy__woofc-badge {

                    align-items: center;
                    color: var(--adfy_woofc_badge_color);
                    padding: 7px 10px 8px 10px;
                    font-size: 11px;
                    line-height: 1;
                    font-weight: normal;
                    border-radius: 15px;
                    background-color: var(--adfy_woofc_badge_background_color);
                }
            }

            .adfy__close-button {

                display: inline-flex;
                align-items: center;
                justify-content: center;

                .adfy__hide-woofc {
                    
                    display: inline-flex;
                    width: 30px;
                    height: 30px;
                    padding: 0;
                    margin: 0;
                    line-height: 1;
                    font-weight: normal;
                    fill: #C7C7C7;
                    color: #C7C7C7;
                    border: none;
                    border-radius: 100%;
                    background-color: transparent;

                    svg {

                        width: 22px;
                        height: 22px;
                    }

                    &:hover {

                        fill: #888888;
                        color: #888888;
                        border: none;
                        background-color: transparent;
                    }
                }
            }
        }

        .adfy__woofc-shipping-bar {

            @include flex();
            flex-direction: column;
            margin-bottom: 40px;

            .adfy__woofc-shipping-text {

                font-size: 15px;
                padding: 0;
                margin: 0 0 10px 0;
                font-weight: normal;

                .amount {

                    font-weight: bold;
                }
            }

            .progress-bars {

                position: relative;

                .shipping-bar {

                    position: relative;
                    display: block;
                    height: 8px;
                    width: 100%;
                    border-radius: 10px;
                    background-color: #EEEEEE;
                }

                .progress-bar {

                    position: absolute;
                    left: 0;
                    top: 0;
                    right: auto;
                    z-index: 2;
                    background: #0072ff; 
                    background: -webkit-linear-gradient(to left, #0072ff, #00a1ff);
                    background: linear-gradient(to left, #0072ff, #00a1ff);
                    transition: all 1s cubic-bezier(0.4, 0, 1, 1);
                }
            }
        }

        .adfy__woofc-content {

            flex: 1 1 auto;
            @include flex();
            flex-direction: column;
            gap: 35px;
            @include breakpoint(mobile) {

                gap: 30px;
            }

            .adfy__woofc-item {

                @include grid();
                grid-template-columns: 100px auto;
                column-gap: 20px;
                row-gap: 20px;

                .thumb {

                    position: relative;
                    width: 100%;
                    max-width: 100%;

                    .adfy__woofc-link {

                        display: block;

                        > img {

                            border-radius: 2px;
                            border: 1px solid var(--adfy_woofc_border_color);
                        }
                    }

                    .adfy__woofc-remove-cart-item {

                        position: absolute;
                        left: -15px;
                        top: -12px;
                        z-index: 1;
                        display: inline-flex;
                        justify-content: center;
                        align-items: center;
                        border: none;
                        margin: 0;
                        padding: 0;
                        height: 26px;
                        width: 26px;
                        color: #878787;
                        border-radius: 100%;
                        background-color: #E9E9E9;
                        @include breakpoint(mobile) {

                            height: 24px;
                            width: 24px;
                        }

                        svg {

                            width: 14px;
                            height: 14px;
                        }
                    }
                }

                .adfy__woofc-item-content {

                    .adfy__woofc-item-title {

                        .woocommerce-loop-product__title {

                            display: block;
                            font-size: 15px;
                            line-height: 1.3;
                            font-weight: normal;
                            color: #444444;
                            padding: 0;
                            margin: 0 0 10px 0;
                            @include breakpoint(mobile) {

                                margin-bottom: 5px;
                            }

                            .adfy__woofc-link {

                                color: #444444;
                            }
                        }
                    }

                    .adfy__woofc-item-price {

                        margin-bottom: 10px;

                        .quantity {

                            display: inline-flex;
                            gap: 2px;
                            align-items: center;
                            color: #444444;
                            font-size: 14px;
                            font-weight: bold;
                        }
                    }

                    .adfy__woofc-quantity {

                        display: block;

                        .adfy__woofc-quantity-form {

                            @include flex();
                            align-items: center;
                            justify-content: center;
                            margin: 0;
                            padding: 0;
                            width: 95px;
                            padding: 2px 10px;
                            border: 1px solid var(--adfy_woofc_border_color);
                            border-radius: 25px;

                            .adfy__woofc-quantity-input-field {

                                display: inline-flex;
                                justify-content: center;
                                align-items: center;
                                text-align: center;
                                width: 30px;
                                max-width: 50px;
                                height: 30px;
                                padding: 0;
                                margin: 0;
                                border: none;
                                outline: none;
                                color: #444444;
                                font-size: 12px;
                                font-weight: normal;
                                will-change: width;
                                box-shadow: none;
                                border-radius: 3px;
                                background: transparent;
                                background-color: transparent;
                                -webkit-appearance: none;
                                -moz-appearance: none;
                                @include transation_delay();

                                &:focus {

                                    outline: 0;
                                }
                            }

                            .adfy__woofc-quantity-input-button {

                                display: inline-flex;
                                justify-content: center;
                                align-items: center;
                                font-size: 16px;
                                padding: 0;
                                margin: 0;
                                font-weight: normal;
                                line-height: 1;
                                height: 20px;
                                width: 20px;
                                fill: #444444;
                                color: #444444;
                                border-radius: 30px;
                                background-color: transparent;

                                svg {

                                    height: 14px;
                                    height: 14px;
                                }
                            }
                        }
                    }
                }
            }
        }

        .adfy__woofc-colophon {

            margin-top: 60px;
            padding-top: 30px;
            border-top: 2px solid var(--adfy_woofc_border_color);

            .adfy__woofc-actions {

                @include flex();
                flex-direction: column;
                gap: 15px;
                width: 100%;

                .adfy__woofc-button {

                    @include flex();
                    align-items: center;
                    justify-content: center;
                    padding: 20px;
                    height: auto;
                    border-radius: 30px;

                    @include breakpoint(medium) {

                        padding: 15px;
                    }
                }

                .proceed-to-checkout {

                    background: #0072ff; 
                    background: -webkit-linear-gradient(to left, #0072ff, #00a1ff);
                    background: linear-gradient(to left, #0072ff, #00a1ff);
                }
            }
        }
    }
}

#adfy__floating-cart[data_type="drawer"] {

    top: 0;
    bottom: 0;
    height: 100%;
    min-height: 100vh;
    width: 500px;
    @include visibility_visible();
    @include breakpoint (mobile) {

        width: 93vw;
    }
}

/* -------------------------------------------------------------------------- 
	#. Position
  -------------------------------------------------------------------------- */

#adfy__floating-cart[data_type="modal"] {

    z-index: var(--adfy_woo_floating_cart_zindex);
}

#adfy__floating-cart[data_type="drawer"][data_position="left"] {

    right: auto;
    left: -100vw;
}

#adfy__floating-cart[data_type="drawer"][data_position="right"] {

    left: auto;
    right: -100vw;
}

/* -------------------------------------------------------------------------- 
	#. Visiblity
  -------------------------------------------------------------------------- */

.adfy__woofc-visible #adfy__floating-cart {

    z-index: var(--adfy_woo_floating_cart_zindex);
    box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.3);
}

.adfy__woofc-visible #adfy__floating-cart[data_type="drawer"][data_position="left"] {

    right: auto;
    left: 0;
}

.adfy__woofc-visible #adfy__floating-cart[data_type="drawer"][data_position="right"] {

    left: auto;
    right: 0;
}

