/* -------------------------------------------------------------------------- 
	#. Floating Cart Button
  -------------------------------------------------------------------------- */
#adfy__woofc-trigger {
  position: fixed;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  left: auto;
  right: 100px;
  bottom: 100px;
  z-index: var(--adfy_woofc_trigger_button_zindex);
  fill: #555555;
  color: #555555;
  cursor: pointer;
  background-color: white;
  border-radius: 0;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.3);
  transition: all 0.5s ease;
}
#adfy__woofc-trigger .icon svg {
  height: 20px;
  width: 20px;
}
#adfy__woofc-trigger .badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: -15px;
  top: -10px;
  height: 23px;
  width: 23px;
  font-size: 13px;
  font-weight: normal;
  line-height: 1;
  color: white;
  border-radius: 3px;
  background-color: var(--adfy_woofc_primary_color);
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.1);
}
#adfy__woofc-trigger:hover {
  fill: #898989;
  color: #898989;
  background-color: #DDD;
  box-shadow: 0px 0px 60px rgba(0, 0, 0, 0.1);
}
#adfy__woofc-trigger:focus {
  outline: none;
}

/* -------------------------------------------------------------------------- 
	#. Design
  -------------------------------------------------------------------------- */
#adfy__woofc-trigger[data_style=round] {
  border-radius: 100%;
}
#adfy__woofc-trigger[data_style=round] .badge {
  top: -5px;
  left: -10px;
  border-radius: 100%;
}

#adfy__woofc-trigger[data_style=rounded] {
  border-radius: 5px;
}
#adfy__woofc-trigger[data_style=rounded] .badge {
  top: -5px;
  left: -10px;
}

/* -------------------------------------------------------------------------- 
	#. Label
  -------------------------------------------------------------------------- */
#adfy__woofc-trigger[data_label=false] .label {
  display: none;
}

/* -------------------------------------------------------------------------- 
	#. Icon
  -------------------------------------------------------------------------- */
#adfy__woofc-trigger[data_icon=false] .icon {
  display: none;
}

/* -------------------------------------------------------------------------- 
	#. General button
  -------------------------------------------------------------------------- */
.adfy__woofc-button {
  display: inline-flex;
  gap: 5px;
  align-items: center;
  font-size: 15px;
  font-weight: normal;
  line-height: 1;
  fill: white;
  color: white;
  padding: 15px 25px;
  border-radius: 3px;
  background-color: #4823ff;
}

.adfy__woofc-button.continue-shopping {
  fill: #878787;
  color: #878787;
  background-color: #CFCFCF;
}

/* -------------------------------------------------------------------------- 
	#. Floating Cart
  -------------------------------------------------------------------------- */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

#adfy__floating-cart {
  display: block;
  position: fixed;
  color: #444444;
  background: white;
  background-color: white;
}
#adfy__floating-cart .adfy_woofc-inner {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex-direction: column;
  height: 100%;
  width: 100%;
  position: relative;
  padding: 30px;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-header {
  display: grid;
  grid-template-columns: auto 30px;
  column-gap: 20px;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 30px;
  padding-bottom: 15px;
  border-bottom: 1px solid var(--adfy_woofc_border_color);
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-header .adfy__woofc-title {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 8px;
  align-items: center;
  font-size: 20px;
  margin: 0;
  padding: 0;
  color: #444444;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-header .adfy__woofc-title .adfy__woofc-badge {
  align-items: center;
  color: var(--adfy_woofc_badge_color);
  padding: 7px 10px 8px 10px;
  font-size: 11px;
  line-height: 1;
  font-weight: normal;
  border-radius: 15px;
  background-color: var(--adfy_woofc_badge_background_color);
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-header .adfy__close-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-header .adfy__close-button .adfy__hide-woofc {
  display: inline-flex;
  width: 30px;
  height: 30px;
  padding: 0;
  margin: 0;
  line-height: 1;
  font-weight: normal;
  fill: #C7C7C7;
  color: #C7C7C7;
  border: none;
  border-radius: 100%;
  background-color: transparent;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-header .adfy__close-button .adfy__hide-woofc svg {
  width: 22px;
  height: 22px;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-header .adfy__close-button .adfy__hide-woofc:hover {
  fill: #888888;
  color: #888888;
  border: none;
  background-color: transparent;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-shipping-bar {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex-direction: column;
  margin-bottom: 40px;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-shipping-bar .adfy__woofc-shipping-text {
  font-size: 15px;
  padding: 0;
  margin: 0 0 10px 0;
  font-weight: normal;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-shipping-bar .adfy__woofc-shipping-text .amount {
  font-weight: bold;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-shipping-bar .progress-bars {
  position: relative;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-shipping-bar .progress-bars .shipping-bar {
  position: relative;
  display: block;
  height: 8px;
  width: 100%;
  border-radius: 10px;
  background-color: #EEEEEE;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-shipping-bar .progress-bars .progress-bar {
  position: absolute;
  left: 0;
  top: 0;
  right: auto;
  z-index: 2;
  background: #0072ff;
  background: -webkit-linear-gradient(to left, #0072ff, #00a1ff);
  background: linear-gradient(to left, #0072ff, #00a1ff);
  transition: all 1s cubic-bezier(0.4, 0, 1, 1);
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-content {
  flex: 1 1 auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex-direction: column;
  gap: 35px;
  /* Wide */
}
@media (max-width: 575px) {
  #adfy__floating-cart .adfy_woofc-inner .adfy__woofc-content {
    gap: 30px;
  }
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-content .adfy__woofc-item {
  display: grid;
  grid-template-columns: 100px auto;
  column-gap: 20px;
  row-gap: 20px;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-content .adfy__woofc-item .thumb {
  position: relative;
  width: 100%;
  max-width: 100%;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-content .adfy__woofc-item .thumb .adfy__woofc-link {
  display: block;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-content .adfy__woofc-item .thumb .adfy__woofc-link > img {
  border-radius: 2px;
  border: 1px solid var(--adfy_woofc_border_color);
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-content .adfy__woofc-item .thumb .adfy__woofc-remove-cart-item {
  position: absolute;
  left: -15px;
  top: -12px;
  z-index: 1;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border: none;
  margin: 0;
  padding: 0;
  height: 26px;
  width: 26px;
  color: #878787;
  border-radius: 100%;
  background-color: #E9E9E9;
  /* Wide */
}
@media (max-width: 575px) {
  #adfy__floating-cart .adfy_woofc-inner .adfy__woofc-content .adfy__woofc-item .thumb .adfy__woofc-remove-cart-item {
    height: 24px;
    width: 24px;
  }
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-content .adfy__woofc-item .thumb .adfy__woofc-remove-cart-item svg {
  width: 14px;
  height: 14px;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-content .adfy__woofc-item .adfy__woofc-item-content .adfy__woofc-item-title .woocommerce-loop-product__title {
  display: block;
  font-size: 15px;
  line-height: 1.3;
  font-weight: normal;
  color: #444444;
  padding: 0;
  margin: 0 0 10px 0;
  /* Wide */
}
@media (max-width: 575px) {
  #adfy__floating-cart .adfy_woofc-inner .adfy__woofc-content .adfy__woofc-item .adfy__woofc-item-content .adfy__woofc-item-title .woocommerce-loop-product__title {
    margin-bottom: 5px;
  }
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-content .adfy__woofc-item .adfy__woofc-item-content .adfy__woofc-item-title .woocommerce-loop-product__title .adfy__woofc-link {
  color: #444444;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-content .adfy__woofc-item .adfy__woofc-item-content .adfy__woofc-item-price {
  margin-bottom: 10px;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-content .adfy__woofc-item .adfy__woofc-item-content .adfy__woofc-item-price .quantity {
  display: inline-flex;
  gap: 2px;
  align-items: center;
  color: #444444;
  font-size: 14px;
  font-weight: bold;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-content .adfy__woofc-item .adfy__woofc-item-content .adfy__woofc-quantity {
  display: block;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-content .adfy__woofc-item .adfy__woofc-item-content .adfy__woofc-quantity .adfy__woofc-quantity-form {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 0;
  width: 95px;
  padding: 2px 10px;
  border: 1px solid var(--adfy_woofc_border_color);
  border-radius: 25px;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-content .adfy__woofc-item .adfy__woofc-item-content .adfy__woofc-quantity .adfy__woofc-quantity-form .adfy__woofc-quantity-input-field {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 30px;
  max-width: 50px;
  height: 30px;
  padding: 0;
  margin: 0;
  border: none;
  outline: none;
  color: #444444;
  font-size: 12px;
  font-weight: normal;
  will-change: width;
  box-shadow: none;
  border-radius: 3px;
  background: transparent;
  background-color: transparent;
  -webkit-appearance: none;
  -moz-appearance: none;
  transition: all 0.5s ease;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-content .adfy__woofc-item .adfy__woofc-item-content .adfy__woofc-quantity .adfy__woofc-quantity-form .adfy__woofc-quantity-input-field:focus {
  outline: 0;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-content .adfy__woofc-item .adfy__woofc-item-content .adfy__woofc-quantity .adfy__woofc-quantity-form .adfy__woofc-quantity-input-button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  padding: 0;
  margin: 0;
  font-weight: normal;
  line-height: 1;
  height: 20px;
  width: 20px;
  fill: #444444;
  color: #444444;
  border-radius: 30px;
  background-color: transparent;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-content .adfy__woofc-item .adfy__woofc-item-content .adfy__woofc-quantity .adfy__woofc-quantity-form .adfy__woofc-quantity-input-button svg {
  height: 14px;
  height: 14px;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-colophon {
  margin-top: 60px;
  padding-top: 30px;
  border-top: 2px solid var(--adfy_woofc_border_color);
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-colophon .adfy__woofc-actions {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex-direction: column;
  gap: 15px;
  width: 100%;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-colophon .adfy__woofc-actions .adfy__woofc-button {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding: 20px;
  height: auto;
  border-radius: 30px;
  /* Wide */
}
@media (max-width: 991px) {
  #adfy__floating-cart .adfy_woofc-inner .adfy__woofc-colophon .adfy__woofc-actions .adfy__woofc-button {
    padding: 15px;
  }
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-colophon .adfy__woofc-actions .proceed-to-checkout {
  background: #0072ff;
  background: -webkit-linear-gradient(to left, #0072ff, #00a1ff);
  background: linear-gradient(to left, #0072ff, #00a1ff);
}

#adfy__floating-cart[data_type=drawer] {
  top: 0;
  bottom: 0;
  height: 100%;
  min-height: 100vh;
  width: 500px;
  opacity: 1;
  visibility: visible;
  /* Wide */
}
@media (max-width: 575px) {
  #adfy__floating-cart[data_type=drawer] {
    width: 93vw;
  }
}

/* -------------------------------------------------------------------------- 
	#. Position
  -------------------------------------------------------------------------- */
#adfy__floating-cart[data_type=modal] {
  z-index: var(--adfy_woo_floating_cart_zindex);
}

#adfy__floating-cart[data_type=drawer][data_position=left] {
  right: auto;
  left: -100vw;
}

#adfy__floating-cart[data_type=drawer][data_position=right] {
  left: auto;
  right: -100vw;
}

/* -------------------------------------------------------------------------- 
	#. Visiblity
  -------------------------------------------------------------------------- */
.adfy__woofc-visible #adfy__floating-cart {
  z-index: var(--adfy_woo_floating_cart_zindex);
  box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.3);
}

.adfy__woofc-visible #adfy__floating-cart[data_type=drawer][data_position=left] {
  right: auto;
  left: 0;
}

.adfy__woofc-visible #adfy__floating-cart[data_type=drawer][data_position=right] {
  left: auto;
  right: 0;
}

/* -------------------------------------------------------------------------- 
	#. Floating Cart Mask
  -------------------------------------------------------------------------- */
#adfy__woofc-overlay {
  display: block;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.8);
  opacity: 0;
  visibility: hidden;
  transition: all 0.5s ease;
}

.adfy__woofc-visible #adfy__woofc-overlay {
  opacity: 1;
  visibility: visible;
  z-index: var(--adfy_woofc_overlaymask_zindex);
}

/* -------------------------------------------------------------------------- 
	#. Utility Class
  -------------------------------------------------------------------------- */
.adfy__hide-woofc {
  cursor: pointer;
}

/* -------------------------------------------------------------------------- 
	#. Slide to left
  -------------------------------------------------------------------------- */
@keyframes adfyWooFCSlideToLeft {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100vw);
  }
}